import React, { Component } from 'react';
import { Query } from 'react-apollo';
import Error from '../ErrorMessage';
import Link from 'next/link';
import Form from '../styles/Form';
import { format } from 'date-fns';
import { BigStyledPage } from '../styles/StyledPage';
import styled from 'styled-components';
import { singleCandidateQuery } from '../queries&Mutations&Functions/Queries';

const UpdateBtn = styled.button`
	background-color: #379;
	border-radius: 10px;
	font-size: .3rem;
	border: none;
	outline: none;
	a {
		color: white;
	}
`;

const DivBtn = styled.div`
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 1rem;
`;

const ImageInfo = styled.div`
	 
	 display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
   
`;

const BottomInfo = styled.div`
    text-align: left;
	padding: 0.2rem;
	margin: .5rem auto;
    font-size: 1.1rem;
    line-height: 0.3rem;
	min-width:20rem;
	/* align-items:center;
	justify-content:left; */
  
`;

const CandImg = styled.div`
	/* padding-left: 3.5rem; */
	display: flex;
	flex-direction: column;
	margin-bottom: .5rem;
	img {
		margin-top: .5rem;
		margin-left: .5rem;
		height: 15rem;
		width: 15rem;
		border-radius: 0.7rem;
		background-size: contain;
		background-position: center;
	}
`;

class SingleCandidate extends Component {
	render() {
		return (
			<Query query={singleCandidateQuery} variables={{ id: this.props.id }}>
				{({ data, loading, error }) => {
					const { candidate } = data;
					const {
						momName,
						dadName,
						candCode,
						image,
						cand1stName,
						cand2ndName,
						cand3rdName,
						email,
						phoneNumb,
						placeOfBirth,
						gender: { genderName },
						dateOfBirth,
						birthCertNumber
					} = candidate;
					{
						loading && <p>Loading...</p>;
					}
					{
						error && <Error error={error} />;
					}
					return (
						<BigStyledPage>
							<Form>
								<h4>Info Candidat</h4>
								<fieldset>
									<ImageInfo>
										<BottomInfo>
											<p>
												<strong>Nom:</strong> <span>{cand1stName}</span>
											</p>
											<p>
												<strong>Prenoms: </strong>
												<span> {cand2ndName}</span>
											</p>

											<p>
												<strong>Autres Noms:</strong>
												<span> {cand3rdName}</span>
											</p>
											<p>
												<strong>Naissance:</strong>
												<hr />
											</p>
											<p>
												<strong>Date:</strong>
												<span> {format(dateOfBirth, 'd MMMM, YYYY')}</span>
											</p>
											<p>
												<strong>Lieu:</strong> <span>{placeOfBirth}</span>
											</p>
											<p>
												<strong>Sexe: </strong>
												<span> {genderName}</span>
											</p>

											<p>
												<strong>Nom des parents:</strong>
												<hr />
											</p>
											<p>
												<strong>Père:</strong>
												<span> {dadName}</span>
											</p>
											<p>
												<strong>Mère:</strong>
												<span> {momName}</span>
											</p>

											<p>
												<strong>Acte Naissance:</strong>
												<hr />
											</p>
											<p>
												<strong>No:</strong>
												<span> {birthCertNumber}</span>
											</p>
											<p>
												<strong>Tel: </strong> <span>{phoneNumb}</span>
											</p>
										</BottomInfo>
										<BottomInfo>
											<p>
												<strong>Email:</strong>
												<span> {email}</span>
											</p>

											<p>
												<strong>Code:</strong> <span> {candCode}</span>
											</p>
											<CandImg>
												<div>{candidate.image && <img src={image} alt="Upload image" />}</div>
											</CandImg>
											<DivBtn>
												<UpdateBtn>
													<Link
														href={{
															pathname: '../updates/updateCand',
															query: { id: this.props.id }
														}}
													>
														<a target="_blank"  >Modifier </a>
													</Link>
												</UpdateBtn>
												<UpdateBtn>
													<Link
														href={{
															pathname: '../creates/newCand'
														}}
													>
														<a target="_blank"  >Candidat Suivant</a>
													</Link>
												</UpdateBtn>
											</DivBtn>
										</BottomInfo>
									</ImageInfo>
								</fieldset>
							</Form>
						</BigStyledPage>
					);
				}}
			</Query>
		);
	}
}

export default SingleCandidate;
